import React, { Component } from 'react';
import Sound from 'react-sound';

import '../style/StartPage.css';

import {Link} from 'react-router-dom';

class EndPage extends Component {
	render() {
		let image = require('../Design/assets/Group 2.png');
		let lpString = localStorage.getItem('lastPlayer');
		let lastPlayer = JSON.parse(lpString);
		let sound = lastPlayer.points > 0 ? require('../sounds/applodisment.mp3') : require('../sounds/zvuk-razocharovaniya.mp3');
		
		return (
			<div className="start-game-container-center">
				<div class='img-container'>
					<img src={image} alt='EndGameImg'/>
				</div>
				<h1 className='title'>Поздравляем, {lastPlayer.name}!</h1>
				<h1 className='title'>Ваш итоговый счет: {lastPlayer.points}</h1>
				<Link to='/GamePage'>
					<div data-tid="EndGame-retryGame" className="btn rerty-game-btn">Еще раз</div>
				</Link>
				<Link to='/LeadersPage'>
					<div data-tid="EndGame-retryGame" className="btn rerty-game-btn">Таблица лидеров</div>
				</Link>
				<Sound url={sound}
					playStatus={Sound.status.PLAYING} />
			</div>

		)
	}
}

export default EndPage;


