import React, { Component } from 'react';
import '../style/Card.css';
import ReactCardFlip from 'react-card-flip';

class Card extends Component {
	constructor(props){
		super(props);
		this.cardImg = this.props.image;
		this.cardUndersideImg = 'cardUnderside.png';
		this.handleOnClick = this.handleOnClick.bind(this);	
		this.deleteCard = this.deleteCard.bind(this);
		this.state = { shouldDelete: false };
	}
	
	shouldComponentUpdate(nextProps) {
		if(this.props.isOpen !== nextProps.isOpen || this.props.isHidden !== nextProps.isHidden){
			if(this.props.isHidden !== nextProps.isHidden)
				setTimeout(() => this.deleteCard(), 400);
			return true;
		}
		return false;
	}
	handleOnClick(e) {
		this.props.onOpen(this.props.id);
	}

	deleteCard() {
		this.setState({ shouldDelete: true});
	}
	
	render() {
		let cardImg = require(`../Cards/${this.cardImg}`);
		let cardUndersideImg = require(`../Cards/${this.cardUndersideImg}`);
		
		let hiddenStyle = this.props.isHidden? {transitionDuration: '.4s', opacity: 0} : {transitionDuration: '.4s', opacity: 1};
		
		let Card = this.state.shouldDelete ? <div></div> : (<div id={this.props.id + ''} className='Card' style={hiddenStyle} onClick={this.handleOnClick}>
 							<ReactCardFlip isFlipped={!this.props.isOpen}>
								<div data-tid="Card" key="front">
									<div className='img-container'>
										<img src={cardImg} alt={this.cardImg}/>
									</div>
								</div>
								<div data-tid="Card-flipped" key="back">
									<div className='img-container'>
										<img src={cardUndersideImg} alt={this.cardUndersideImg}/>
									</div>
								</div>
							</ReactCardFlip>
						</div>)
		
		return Card;

	}
};

export default Card;


