import React, { Component } from 'react';
import Sound from 'react-sound';
import '../style/StartPage.css';

import {Link} from 'react-router-dom';


class StartPage extends Component {
	
	render() {
		let image = require('../Design/assets/StartGame.png');
		let sound = require('../sounds/Tusovka_kart.mp3');
		
		return (
			<div data-tid="App" className="start-game-container-center">
				<div class='img-container'>
					<img src={image} alt='StartGameImg'/>
				</div>
				<h1 className='title'>MEMORY GAME</h1>
				<Link to='/GamePage'>
					<div data-tid="NewGame-startGame" className="btn start-game-btn">Начать игру</div>
				</Link>
				<Sound url={sound}
					playStatus={Sound.status.PLAYING} autoLoad={true} loop={true}
      	/>
			</div>

		)
	}
}

export default StartPage;


