import React, { Component } from 'react';
import '../style/LeadersPage.css';

import {Link} from 'react-router-dom';

class LeadersPage extends Component {
	render() {
		let players = [];
		for(let i = 0; i < localStorage.length - 1; i++){
			players[i] = JSON.parse(localStorage.getItem(i));
		}
		return (
			<div className="start-game-container-center">
				{
					players.map(function(player, index){
						return <div className='record'>
							{index + 1}. {player.name} : {player.points}
						</div>
					})
				}
				<Link to='/GamePage'>
						<div data-tid="EndGame-retryGame" className="btn rerty-game-btn">Еще раз</div>
				</Link>
				<Link to='/EndPage'>
						<div data-tid="EndGame-retryGame" className="btn rerty-game-btn">Обратно</div>
				</Link>
			</div>

		)
	}
}

export default LeadersPage;


