import React, { Component } from 'react';
import '../style/RestartBtn.css';
import {Link} from 'react-router-dom';

class RestartBtn extends Component {
	render() {
		return (
			<Link to='/'>
				<div data-tid="Menu-newGame" className='restart-btn' onClick={this.props.restartGame}>Начать заново</div>
			</Link>
		)
	}
}

export default RestartBtn;


